.me {
    height: 100vh;
    padding: 0 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 3%;
    margin-bottom: 10%;
  }
  
  .me-overlay {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10% 0 0;
  }
  
  .description {
    width: 75%;
  }
  
  .my-image {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 35%;
    animation-name: floating;
    animation-duration: 6s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
  }
  
  .title {
    font-size: 2rem;
    color: white;
    margin: 5px;
    font-family:Arial, Helvetica, sans-serif;
  }
  
  .tagline {
    font-size: 1.1rem;
    color: #c3073f;
    margin: 2 0 0 0;
    font-family: 'Orbit', sans-serif;
    text-decoration: double;
  }
  
  .paragraph {
    font-size: 1rem;
    color: white;
    opacity: 0.8;
    font-weight: normal;
    line-height: 2.2rem;
    margin: 2rem 0;
    text-align: justify;
    width: 85%;
    font-family: Arial;
  }
  
  .my-btns {
    display: flex;
    align-items: center;
    gap: 2rem;
    color: #c3073f;
  }
  
  .cv {
    color: #fff;
    background-color: #c3073f;
  }
  
  .cv:hover {
    color: #fff;
    background-color: #ee4c7c;
  }
  
  .cover-letter {
    
    color: white;
    background-color: #ee4c7c;
  }
  
  .social-icons {
    display: flex;
    align-items: center;
    gap: 2.5rem;
  }
  
  .social-icons a {
    color: #fafafa;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    text-decoration: none;
    font-size: 2.5rem;
  }
  
  .social-icons a i {
    transition: 0.3s ease-in-out;
  }
  
  .social-icons a:hover i {
    transition: 0.3s ease-in-out;
    transform: scale(1.8);
  }
  @keyframes floating {
    0% {
      transform: translate(0, 0);
    }
  
    50% {
      transform: translate(0, 15px);
    }
  
    100% {
      transform: translate(0, -0);
    }
  }
  
  